body {
  background-color: rgb(14, 21, 27);
}
.container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; /* Align items to the top */
  padding: 20px;
  max-width: 1200px; /* Optional: Limit the container's width */
  margin: 0 auto; /* Center the container */
}
.content {
  flex: 1; /* Take up remaining space */
  margin-right: 20px; /* Space between content and image */
}
.content h3,
.content h4,
.content h5 {
  margin: 2px 0;
}


h1 {
  font-size: 30px;
  text-align: left;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
}
@keyframes typing {
  from {
      width: 0
  }

  to {
      width: 100%
  }
}
@keyframes blink {
  from {
      border-color: transparent
  }

  to {
      border-color: darkgreen;
  }
}

p {
  font-size: 20px;
  text-align: left;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  font-size: 20px;
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
}


h2 {
  font-size: 50px;
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
}

h3 {
  font-size: 30px;
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
}

  h4 {
    font-size: 25px;
    font-weight: normal;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
    color: #FFFFFF;
  }
  h5 {
    font-size: 22px;
    font-weight: normal;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
    color: #FFFFFF;
  }
@keyframes cursor-blink {
  0% {
      opacity: 0;
  }
}

img {
  display: block;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  align-content: center;
}

/* Image Container Styles */
.image-container {
  flex: 0 0 300px; /* Fixed width; adjust as needed */
}

.image-container img {
  width: 100%;
  height: auto;
  border-radius: 8px; /* Optional: Rounded corners */
  object-fit: cover; /* Ensure the image covers the container without distortion */
}



/* Looping Words Animation Styles */
.looping-words {
  position: relative;
  height: 30px; /* Adjust based on font size */
  margin-top: 20px; /* Space above the animation */
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
  font-size: 1.8em; /* Adjust as needed */
  font-weight: bold;
}

.looping-words span {
  position: absolute;
  width: 100%;
  opacity: 0;
  animation: fade 12s infinite;
}

.looping-words span:nth-child(1) {
  animation-delay: 0s;
}

.looping-words span:nth-child(2) {
  animation-delay: 3s;
}

.looping-words span:nth-child(3) {
  animation-delay: 6s;
}

.looping-words span:nth-child(4) {
  animation-delay: 9s; /* Delay for the fourth word */
}



@keyframes fade {
  0% { opacity: 0; }
  8.33% { opacity: 1; } /* Fade in */
  25% { opacity: 1; }    /* Stay visible */
  33.33% { opacity: 0; } /* Fade out */
  100% { opacity: 0; }
}


marquee {
  font-size: 30px;
  text-align: center;
  background-color: #A16E00;
  margin-top: 10px;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
}
